<template>
  <main-layout menuActiveIndex="tag">
    <h3>Tag </h3>
    <fish-row gutter="1">
      <fish-col span="8">
        <code-card title="" desc="@close">
          <template slot="demo">
            <fish-tag index="1"></fish-tag>
            <fish-tag index="2">TAG</fish-tag>
            <fish-tag index="3" @close="{}"></fish-tag>
            <fish-tag index="3" color="primary"></fish-tag>
          </template>
          <pre v-highlightjs slot="codeHtml"><code class="xml">&lt;fish-tag index=&quot;1&quot;&gt;&lt;/fish-tag&gt;
&lt;fish-tag index=&quot;2&quot;&gt;TAG&lt;/fish-tag&gt;
&lt;fish-tag index=&quot;3&quot; @close=&quot;{}&quot;&gt;&lt;/fish-tag&gt;
&lt;fish-tag index=&quot;3&quot; color=&quot;primary&quot;&gt;&lt;/fish-tag&gt;</code></pre>
        </code-card>
      </fish-col>

      <fish-col span="8">
        <code-card title="" desc="">
          <template slot="demo">
            <fish-tags :items="tags" edited @close="groupCloseHandler" @add="addCheckHandler"></fish-tags>
          </template>
          <pre v-highlightjs slot="codeHtml"><code class="xml">&lt;template&gt;
  &lt;fish-tags :items=&quot;tags&quot; edited @close=&quot;groupCloseHandler&quot; @add=&quot;addCheckHandler&quot;&gt;&lt;/fish-tags&gt;
&lt;/template&gt;
&lt;script&gt;
  export default {
    data () { return {tags: ['tag1', 'tag2', 'tag3']} },
    methods: {
      addCheckHandler (v) {
        this.tags.push(v)
      },
      groupCloseHandler (index) {
        this.tags.splice(index, 1)
      }
    }
  }
&lt;/script&gt;</code></pre>
        </code-card>
      </fish-col>

      <fish-col span="8">
        <code-card title="" desc=" @click  Checkbox ">
          <template slot="demo">
            <fish-tags :items="tags1" @click="checkClickHandler"></fish-tags>
          </template>
          <pre v-highlightjs slot="codeHtml"><code class="xml">&lt;template&gt;
  &lt;fish-tags :items=&quot;tags1&quot; @click=&quot;checkClickHandler&quot;&gt;&lt;/fish-tags&gt;
&lt;/template&gt;
&lt;script&gt;
  export default {
    data () { return {tags1: [[true, &#x27;tag1-radio&#x27;], [false, &#x27;tag2-radio&#x27;], [false, &#x27;tag3-radio&#x27;]]} },
    methods: {
      checkClickHandler (event, index, checked) {
        this.tags1.splice(index, 1, [checked, this.tags1[index][1]])
      }
    }
  }
&lt;/script&gt;</code></pre>
        </code-card>
      </fish-col>
    </fish-row>

    <h3>Tags Attributes</h3>
    <div class="fish table attributes">
      <table style="table-layout: auto;">
        <thead>
        <tr>
          <th v-for="column in api_columns">{{column}}</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="item in api_data">
          <td v-for="v in item" v-html="v"></td>
        </tr>
        </tbody>
      </table>
    </div>

    <h3>Tags Events</h3>
    <div class="fish table attributes">
      <table style="table-layout: auto;">
        <thead>
        <tr>
          <th v-for="column in event_columns">{{column}}</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="item in event_data">
          <td v-for="v in item" v-html="v"></td>
        </tr>
        </tbody>
      </table>
    </div>

    <h3>Tag Attributes</h3>
    <div class="fish table attributes">
      <table style="table-layout: auto;">
        <thead>
        <tr>
          <th v-for="column in api_columns">{{column}}</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="item in api_data2">
          <td v-for="v in item" v-html="v"></td>
        </tr>
        </tbody>
      </table>
    </div>
  </main-layout>
</template>
<script>
  import MainLayout from './MainLayout.vue'
  import CodeCard from './CodeCard.vue'

  export default {
    components: {
      CodeCard,
      MainLayout
    },
    data () {
      return {
        api_columns: ['', '', '', ''],
        api_data: [
          ['size', ': <code>mini</code>, <code>tiny</code>, <code>small</code>, <code>medium</code>, <code>large</code>, <code>big</code>, <code>huge</code>, <code>massive</code>', 'String', '-'],
          ['edited', '', 'Boolean', 'false'],
          ['items', '', 'Array', '-']
        ],
        api_data2: [
          ['icon', ', fontawesome icon ', 'String', '-'],
          ['index', ', ', 'string', '-'],
          ['size', '<code>mini</code>, <code>tiny</code>, <code>small</code>, <code>medium</code>, <code>large</code>, <code>big</code>, <code>huge</code>, <code>massive</code>', 'String', '-'],
          ['color', '', 'String', '-']
        ],
        event_columns: ['', '', ''],
        event_data: [
          ['add (v)', '', ''],
          ['close (index)', '', 'index'],
          ['click (index, checked)', '', 'index, checked']
        ],
        tags1: [[true, 'tag1-radio'], [false, 'tag2-radio'], [false, 'tag3-radio']],
        tags: ['tag1', 'tag2', 'tag3']
      }
    },
    methods: {
      checkClickHandler (index, checked) {
        this.tags1.splice(index, 1, [checked, this.tags1[index][1]])
      },
      addCheckHandler (v) {
        this.tags.push(v)
      },
      groupCloseHandler (index) {
        this.tags.splice(index, 1)
      }
    }
  }
</script>



