<template>
  <fish-table :columns="columns" :data="data" :expandedRowRender="(record)=>record"></fish-table>
</template>
<script>
  export default {
    name: 'demo-table-expand',
    data () {
      return {
        columns: [{title: '', key: 'name'}, {title: '', key: 'age'}, {title: '', key: 'address'}],
        data: [
          {name: '', age: 32, address: '1'},
          {name: '', age: 35, address: '5'},
          {name: '', age: 35, address: '5'},
          {name: '', age: 35, address: '5'}
        ]
      }
    }
  }
</script>


